import { Component } from '@angular/core';



@Component({
    selector: 'app-sign-in',
    template: `
        <form (ngSubmit)="onSubmit(formSignIn)" #formSignIn="ngForm">
        <p>Username :</p>    
        <input
                 placeholder="Email"
                 ngModel
                 #txtEmail="ngModel"
                 name="email"
                 required
                 email
            >
            <p *ngIf="txtEmail.touched && txtEmail.errors?.required">
                Email is required
            </p>
            <p *ngIf="txtEmail.touched && txtEmail.errors?.email">
                Email is not valid
            </p>
            <br><br>
            <p>Password :</p>
            <input 
                type="password" 
                placeholder="Password" 
                ngModel 
                #txtPassword="ngModel"
                name="password"
                minlength="6"
                pattern="[a-z]*"
                required
              
            >
            <p *ngIf="txtPassword.touched && txtPassword.errors?.required">
            Email is required
        </p>
        <p *ngIf="txtPassword.touched && txtPassword.errors?.email">
        txtPasswordis not valid
        </p>
    
            <br><br>
            <p>confirmPassword :</p>
            <input 
                type="password" 
                placeholder="RePassword" 
                ngModel 
                #txtPassword="ngModel"
                name="repassword"
                minlength="6"
                pattern="[a-z]*"
            >
            <br><br>
            <div ngModelGroup="subjects">
            <label><input type="checkbox" [ngModel]="false" name="NodeJS"> NodeJS</label>
            <label><input type="checkbox" [ngModel]="false" name="Angular"> Angular</label>
            <label><input type="checkbox" [ngModel]="false" name="ReactJS"> ReactJS</label>
            </div>
            <br><br>
            <button [disabled]="formSignIn.form.invalid">Submit</button>
        </form> 
         
        <p>{{ txtEmail.errors | json }}</p>
        <p>{{ txtPassword.errors | json }}</p>
        <p>{{ formSignIn.value | json }}</p>
      `
       

    
})

export class SignInComponent {
    onSubmit(formSignIn) {
        console.log(formSignIn);
        
    }


}